@import "settings/color";
@import "settings/font";
@import "tools/mixin";
@import "tools/media";

.container__video-body {}

.video-body {
  background-color: $beige;
  padding: 5px 5px 70px;
  height: 100%;
  overflow-y: hidden;
  box-sizing: border-box;
  display: flex;
  min-height: 100%;
  @include tablet {
    padding: 0 0 65px; }
  &_timer {
    justify-content: center;
    align-content: center;
    height: 100%;
    width: 100%;
    @include tablet {
      padding: 0; } } }

.video-body__videos {
  display: flex;
  align-items: flex-start;
  align-content: flex-start;
  flex-wrap: wrap;
  height: 100%;
  width: 100%;
  &_2 {
    display: flex;
    position: relative;
    justify-content: flex-start;
    .video-body__item {
