      height: calc(100% - 10px);
      @include mobile {
        width: calc(100% - 10px);
        height: calc(100% / 2 - 10px); } }
    .video-card__name {
      width: 235px;
      bottom: 7px;
      left: 10px;
      height: 26px;
      @include tabletSmall {
        width: calc(100% - 2px);
        left: 1px;
        bottom: 1px; }
      @include mobile {
        width: 200px; } } }
  &_3 {
    display: flex;
