        height: calc(100% / 2 - 10px); }
      @include mobile {
        width: calc(100% / 2 - 10px);
        height: calc(100% / 5 - 10px); } }
    .video-card__name {
      @include tabletSmall {
        width: calc(100% - 2px);
        left: 1px;
        bottom: 1px; } } }
  &_11 {
