    .video-card__name {
      @include tabletSmall {
        width: calc(100% - 2px);
        left: 1px;
        bottom: 1px; } } }
  &_side {
    @include mobile {
      display: none; } }
  &_active#{&}_2 {
    display: flex;
    .video-body__item {
      height: calc(100% / 4 - 10px);
