      @include mobile-landscape {
        height: calc(100% - 10px); }
      @include mobile {
        height: calc(100% / 2 - 10px);
        width: calc(100% - 10px); } }
    .video-card__name {
      width: calc(100% - 2px);
      left: 1px;
      bottom: 1px; }
    + .video-body__selected-item {
      @include tabletSmall {
        width: 70%; }
      @include mobile {
        width: calc(100% - 10px); } }
    @include mobile {
      position: absolute;
      top: 5px;
      left: 5px;
      z-index: 2;
      width: 95px;
      height: 135px;
      border: none;
      .video-card {
        height: 100%; }
      .video-card__name {
        display: none; } } }
  &_active#{&}_3 {
