      @include mobile {
        height: calc(100% / 3 - 10px); } }
    .video-card__name {
      width: calc(100% - 2px);
      left: 1px;
      bottom: 1px; } }
  &_active#{&}_5 {
    display: flex;
    .video-body__item {
      width: calc(100% - 10px);
      height: calc(100% / 4 - 10px);
      @include mobile-landscape {
        height: calc(100% / 2 - 10px);
        width: calc(100% / 2 - 10px); }
      @include mobile {
        height: calc(100% / 4 - 10px);
        width: calc(100% - 10px); } }
    .video-card__name {
      width: calc(100% - 2px);
      left: 1px;
      bottom: 1px; }
    + .video-body__selected-item {
      width: 80%;
      @include mobile-landscape {
        width: 40%; }
      @include mobile {
        width: 70%; } } }
  &_active#{&}_6 {
    display: flex;
    .video-body__col {
      width: calc(100% / 5);
      height: 100%;
      @include mobile {
        order: -1; } }
    .video-body__col_2 {
      display: flex;
      flex-wrap: wrap;
      width: calc(100% / 5 * 4);
      height: calc(100%);
      @include mobile-landscape {
