          width: calc(100% / 4 * 3 - 10px); } } }
    .video-body__item {
      width: calc(100% - 10px);
      height: calc(100% / 4 - 10px); }
    .video-card__name {
      @include mobile {
        width: calc(100% - 2px);
        left: 1px;
        bottom: 1px; } }
    .video-body__selected-item {
      width: calc(100% - 10px);
      height: calc(100% / 4 * 3 - 10px); } }
  &_active#{&}_8 {
    display: flex;
    .video-body__col {
