.video-body__item {
  margin: 5px 5px;
  width: calc(100% / 4 - 10px);
  height: calc(100% / 3 - 10px);
  @include mobile-landscape {
    width: calc(100% / 5 - 10px);
    height: calc(100% / 2 - 10px); }
  @include mobile {
    width: calc(100% / 2 - 10px);
    height: calc(100% / 5 - 10px); } }


.video-body__selected-item {
  width: 80%;
  flex-shrink: 0;
  height: calc(100% - 10px);
  margin: 5px;
  @include mobile {
    width: 60%; }
  .video-card__name {
    width: 250px;
    bottom: 7px;
    left: 10px;
    @include tabletSmall {
      width: calc(100% - 2px);
      bottom: 1px;
      left: 1px; } } }

.video-body__row {
  display: flex;
  @include tabletSmall {
    flex-direction: column;
    height: 546px;
    flex-wrap: wrap;
    margin-bottom: 10px; } }

.video-body__col {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  @include mobile {
    justify-content: flex-start; } }

.video-body__side-info {
  width: 308px;
  flex-shrink: 0;
  @include mobile {
    order: -1;
    margin-bottom: 20px; } }

.side-info {
  overflow-y: scroll;
  padding-right: 14px;
  padding-left: 40px;
  box-sizing: border-box;
  max-height: 700px;

  &::-webkit-scrollbar {
    width: 4px;
    @include mobile {
      width: auto; } }

  &::-webkit-scrollbar-track {
    background-color: #C4C4C4;
    border-radius: 2px;
    @include mobile {
      background-color: transparent;
      border-radius: 0; } }

  &::-webkit-scrollbar-thumb {
    background-color: $gray;
    border-radius: 2px;
    height: 100px;
    @include mobile {
      background-color: transparent; } }

  @include mobile {
    width: 100%;
    padding: 0 10px; } }

.side-info__heading {
  margin-top: 15px;
  @include fontWSCL(bold, 18px, $gray, 21px); }

.side-info__text {
  margin-top: 37px;
  @include fontWSCL(normal, 15px, $gray, 17px);
  text-align: justify;
  opacity: 0.8; }

.side-info__footer {
  border-top: 3px solid #C69776;
  padding-top: 40px;
  box-sizing: border-box;
  display: flex;
  flex-wrap: wrap;
  margin-top: 70px; }

.side-info__file {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0 10px 10px; }

.side-info__file-img {
  display: flex;
  justify-content: center;
  align-items: center; }

.side-info__file-text {
  @include fontWSCL(normal, 15px, $gray, 17px);
  margin-top: 5px; }
