@import "settings/color";
@import "settings/font";
@import "tools/mixin";
@import "tools/media";

.video-card {
  width: 310px;
  height: 200px;
  box-sizing: border-box;
  position: relative;
  border-radius: 10px;
  border: 1px solid #000;
  cursor: pointer; }

.video-card__video {
  width: 100%;
  height: 100%;
  border-radius: 10px;
  overflow: hidden;
  img {
    width: 100%;
    height: 100%;
    border-radius: 10px;
    object-fit: cover; } }

.video-card__name {
  position: absolute;
  bottom: 1px;
  left: 1px;
  width: calc(100% - 2px);
  height: 36px;
  display: flex;
  align-items: center;
  box-sizing: border-box;
  padding: 5px 10px;
  border: 1px solid #000;
  border-radius: 9px;
  background-color: $yellow;
  @include tabletSmall {
    height: 22px; } }

.video-card__img {
  width: 25px;
  height: 25px;
  margin-right: 10px;
  display: flex;
  @include tabletSmall {
    width: 15px; } }

.video-card__name-text {
  @include fontWSCL(normal, 18px, rgba(0, 0, 0, 0.8), 16px);
  @include tabletSmall {
    font-size: 12px;
    line-height: 10px; }
  @include mobile {
    font-size: 10px;
    line-height: 10px; } }

.video-card__micro {
  position: absolute;
  top: 10px;
  right: 10px; }
